import React, { useEffect, useRef } from 'react';
import { MapContainer, TileLayer, useMap } from "react-leaflet";

const isMobile = () => {
  return window.innerWidth <= 768; // Define mobile para telas menores que 768px
};

// Componente para gerenciar o redimensionamento do mapa
const MapResizeHandler = () => {
  const map = useMap();
  
  useEffect(() => {
    const handleResize = () => {
      // Usa requestAnimationFrame para garantir que o redimensionamento aconteça após a renderização
      requestAnimationFrame(() => {
        map.invalidateSize();
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [map]);

  return null;
};

const MapaBase = ({ children }) => {
  const mapRef = useRef(null);
  
  // Define coordenadas e zoom baseados no dispositivo para visualizar SP
  const defaultPosition = isMobile() ? [-23.5505, -48.6333] : [-23.305, -43.9];
  const defaultZoom = isMobile() ? 6 : 7; // Zoom mais aberto para visualizar mais do estado

  return (
    <div className="h-screen w-screen overflow-hidden">
      <MapContainer
        ref={mapRef}
        center={defaultPosition}
        zoom={defaultZoom}
        className="h-full w-full"
        attributionControl={false}
        zoomSnap={0.25}
        zoomDelta={0.25}
        updateWhenIdle={true}
        updateWhenZooming={false}
        preferCanvas={true}
        maxZoom={18}
        minZoom={4}
      >
        {/* TileLayer com o mapa de fundo otimizado */}
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          detectRetina={true}
          reuseTiles={true}
          edgeBufferTiles={2}
          keepBuffer={10}
          maxNativeZoom={18}
          maxZoom={18}
          tileSize={256}
          zoomOffset={0}
          subdomains={['a', 'b', 'c']}
        />
        
        {/* Handler para gerenciar redimensionamento */}
        <MapResizeHandler />

        {/* Conteúdo adicional do mapa (marcadores, camadas, etc.) */}
        {children}
      </MapContainer>
    </div>
  );
};

export default MapaBase;